@use 'variables' as v;

@mixin phone {
    @media (min-width: v.$phone){
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop){
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopXL){
        @content;
    }
}
@mixin grid($col, $esp) {
    display: grid;
    grid-template-columns: repeat( $col, 1fr);
    gap: $esp;
}
@mixin list-reset {
    margin: 0;
    padding: 0;
    list-style: none;
}