html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Kdam Thmor Pro", sans-serif;
  font-size: 1.6rem;
}
body.pinup-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: rgb(0, 0, 0);
  line-height: 1.5;
  text-align: justify;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 10rem;
  font-family: "Metal Mania", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Metamorphous", sans-serif;
}

h3 {
  font-size: 3.6rem;
  font-family: "Metamorphous", sans-serif;
}

h4 {
  font-size: 3rem;
  font-family: "Metamorphous", sans-serif;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: rgb(0, 0, 0);
}
@media (min-width: 768px) {
  .header.pinup-bar {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
.header h2 {
  color: rgb(255, 0, 0);
  text-align: center;
}
@media (min-width: 768px) {
  .header h2 {
    margin: 0;
  }
}
.header .header__cont {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header__cont {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .menu {
    display: flex;
    gap: 2rem;
  }
}
.menu a {
  display: block;
  text-align: center;
  color: rgb(255, 204, 0);
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .menu a {
    margin: 0;
  }
}
.menu a:hover {
  color: rgb(245, 103, 11);
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(255, 204, 0);
  background: radial-gradient(circle, rgba(255, 204, 0, 0.5) 0%, rgba(255, 0, 0, 0.5) 32%, rgb(0, 0, 0) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.cont__video {
  z-index: 10;
}
.cont__video h1 {
  color: rgb(255, 204, 0);
  margin: 0;
}
.cont__video p {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 0, 0);
  display: inline-block;
  padding: 1rem 2rem;
  font-weight: 700;
  margin: 0;
}

.fest {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .fest {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.fest__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .fest__info {
    margin-top: 0;
  }
}
.fest__info h2 {
  margin-bottom: 2rem;
}
.fest__info .fest__date {
  color: rgb(255, 0, 0);
  font-weight: 700;
}

.lineup {
  background-color: rgb(0, 0, 0);
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: rgb(255, 0, 0);
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .container__scenary {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.container__scenary p {
  color: rgb(0, 0, 0);
}
.container__scenary .scenary {
  padding-top: 2rem;
}
.container__scenary .scenary p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 20rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.icon_a .scenary__name:after {
  background-image: url(../img/icon_a.png);
}

.icon_b .scenary__name:after {
  background-image: url(../img/icon_b.png);
}

.calendary {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendary li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendary li span {
  font-weight: 400;
}

.bg_a {
  background-color: rgb(255, 204, 0);
}
.bg_a li:nth-child(odd) {
  background-color: #b38f00;
}

.bg_b {
  background-color: rgb(245, 103, 11);
}
.bg_b li:nth-child(odd) {
  background-color: #ac4807;
}

.galery {
  padding: 5rem 0;
}
.galery h3 {
  text-align: center;
}

.galery-img {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galery-img {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galery-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galery-img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 100rem;
}

.btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: rgba(250, 250, 250, 0.5);
  color: rgb(0, 0, 0);
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-close:hover {
  cursor: pointer;
}

.tikets {
  padding: 5rem 0;
}
.tikets h3 {
  text-align: center;
  color: rgb(0, 0, 0);
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tikets .container__tikets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: rgb(0, 0, 0);
}
.pase .pase-type {
  text-align: center;
  font-size: 2.5rem;
}
.pase__header, .pase__footer {
  padding: 1rem;
}
.pase__header p, .pase__footer p {
  text-align: center;
  margin: 0;
}
.pase__header {
  background-color: rgb(0, 0, 0);
}
.pase__header p {
  color: rgb(250, 250, 250);
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase__body {
  background-color: rgb(255, 0, 0);
}
.pase__body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase__body ul li, .pase__body ul :nth-child(odd) {
  background-color: rgb(255, 0, 0);
  padding: 1rem;
}
.pase__footer {
  background-color: rgb(255, 204, 0);
}
.pase__footer p {
  font-size: 2rem;
}

.footer {
  background-color: rgb(0, 0, 0);
  padding: 1rem;
}
.footer p {
  color: rgb(250, 250, 250);
  margin: 0;
  text-align: center;
}